@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .inicio {
    background-image: url(../img/header.jpg);
}

.webp .inicio {
    background-image: url(../img/header.webp);
}

.header {
    position: relative;
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0; //va como reloj empieza de arriba a la derecha

    //se hace referencia a inicio de header

    &.inicio {
        background-position: center center;
        background-size: cover; //se toma todo el contenido

                 // Capa oscura encima de la imagen
         &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.7); // Opacidad regulable
            z-index: 1; // Debajo del contenido
        }


        @include m.tablet{
            min-height: 70rem;
        }
        //Se integra en inicio para que en las demas secciones se tomen el mismo estilo de header ya que solo se modifica estilo de inicio 
        .contenido-header {
            @include m.tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header {
    position: relative; // Necesario para que funcione el z-index
    z-index: 2; // Más alto que el ::before
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
        font-family: v.$fuente_logo;
        letter-spacing: 5px;
        text-align: left;
        color: v.$blanco;
        font-size: 600;
        font-weight: v.$bold;
        line-height: 2;

            max-width: 70rem;
    }

}