@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';
// CSS corregido y limpio para .tarjeta-producto
.tarjeta-producto {
  background-color: v.$blanco;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin: 2rem auto;
  font-family: v.$fuente_principal;
  width: 100%;
  max-width: 1200px;
  box-sizing: border-box;
  overflow: hidden; // ← AGREGADO: Previene desbordamiento del contenedor
  
  // Móvil: layout vertical
  display: flex;
  flex-direction: column;
  
  // Desktop: layout horizontal
  @include m.tablet {
    padding: 2rem;
    flex-direction: row;
    gap: 2rem;
    align-items: flex-start;
    min-height: 350px; // ← AGREGADO: Altura mínima consistente
  }
  
  @include m.desktop {
    gap: 3rem;
    padding: 2.5rem;
    min-height: 400px; // ← AGREGADO: Altura mínima consistente
  }

  .img-anuncios {
    width: 100%;
    height: 250px;
    border-radius: 0.8rem;
    object-fit: cover;
    margin-bottom: 1.5rem;
    flex-shrink: 0;
    
    @include m.tablet {
      width: 40%;
      height: 350px;
      margin-bottom: 0;
    }
    
    @include m.desktop {
      width: 45%;
      height: 400px;
    }
  }

  .resumen-propiedad {
    background-color: v.$blanco;
    color: v.$negro;
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    min-width: 0; // ← AGREGADO: Permite contracción del contenido
    overflow: hidden; // ← AGREGADO: Previene desbordamiento

    h2 {
      font-family: v.$fuente_secundaria;
      font-size: 2.2rem;
      margin-bottom: 1rem;
      color: v.$dorado;
      font-weight: v.$bold;
      text-align: center;
      word-wrap: break-word; // ← AGREGADO: Rompe palabras largas
      overflow-wrap: break-word; // ← AGREGADO: Soporte moderno para break-word
      hyphens: auto; // ← AGREGADO: Guiones automáticos
      line-height: 1.2; // ← AGREGADO: Mejor espaciado
      
      @include m.tablet {
        font-size: 2.4rem;
        text-align: left;
      }
      
      @include m.desktop {
        font-size: 2.6rem;
      }
    }

    .detalles-producto {
      display: flex;
      flex-direction: column;
      gap: 0.8rem;
      margin-bottom: 1.5rem;
      flex: 1;
      width: 100%;
      min-width: 0; // ← AGREGADO: Permite contracción
      
      @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
      }
      
      @include m.desktop {
        grid-template-columns: repeat(2, 1fr);
        gap: 1.2rem;
      }
      
      p {
        margin: 0;
        font-size: 1.4rem;
        color: v.$gris-oscuro;
        line-height: 1.4;
        word-wrap: break-word;
        overflow-wrap: break-word; // ← AGREGADO: Soporte moderno
        hyphens: auto; // ← AGREGADO: Guiones automáticos
        min-width: 0; // ← AGREGADO: Permite contracción
        
        @include m.tablet {
          font-size: 1.4rem;
        }
        
        @include m.desktop {
          font-size: 1.5rem;
        }

        strong {
          color: v.$dorado; // ← AGREGADO: Mejora visual para labels
          display: inline-block;
          margin-right: 0.5rem;
        }

        .talla {
          background-color: v.$dorado;
          color: v.$blanco;
          padding: 0.2rem 0.5rem;
          border-radius: 0.3rem;
          font-weight: v.$bold;
          font-size: 1.2rem;
          display: inline-block;
          margin: 0.1rem;
          white-space: nowrap;
          max-width: 60px; // ← AGREGADO: Limita ancho máximo de tallas
          overflow: hidden; // ← AGREGADO: Oculta contenido que se desborda
          text-overflow: ellipsis; // ← AGREGADO: Puntos suspensivos
        }
      }
      
      // SOLUCIÓN ESPECÍFICA PARA LAS TALLAS
      .tallas-disponibles {
        @include m.tablet {
          grid-column: 1 / -1; // Ocupa ambas columnas del grid
        }
        
        @include m.desktop {
          grid-column: 1 / -1; // Ocupa ambas columnas del grid
        }
        
        // ← AGREGADO: Contenedor flex para las tallas
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        gap: 0.3rem;
        
        .talla {
          margin: 0.2rem 0.3rem 0.2rem 0;
          flex-shrink: 0;
          max-width: calc(25% - 0.3rem); // ← AGREGADO: Máximo 4 tallas por fila
          
          @include m.tablet {
            max-width: calc(20% - 0.3rem); // ← AGREGADO: Máximo 5 tallas por fila en tablet
          }
          
          @include m.desktop {
            max-width: calc(16.66% - 0.3rem); // ← AGREGADO: Máximo 6 tallas por fila en desktop
          }
        }
      }
    }

    .iconos-caracteristicas {
      margin: 1rem 0;
      display: flex;
      justify-content: center;
      
      @include m.tablet {
        justify-content: flex-start;
      }

      li {
        display: flex;
        align-items: center;
        gap: 0.8rem;
        font-size: 1.4rem;
        color: v.$dorado;
        font-weight: v.$bold;
        background-color: rgba(v.$dorado, 0.1);
        padding: 0.4rem;
        border-radius: 0.5rem;
        width: 100%;
        justify-content: center;
        
        @include m.tablet {
          font-size: 1.3rem;
          width: auto;
          padding: 0.5rem;
        }
        
        @include m.desktop {
          font-size: 1.4rem;
          padding: 0.4rem;
        }

        .icono {
          width: 1.6rem;
          height: 1.6rem;
          flex-shrink: 0;
          filter: brightness(0) saturate(100%) invert(47%) sepia(45%) saturate(1012%) hue-rotate(358deg) brightness(89%) contrast(87%);
          
          @include m.tablet {
            width: 1.8rem;
            height: 1.8rem;
          }
          
          @include m.desktop {
            width: 1.6rem;
            height: 1.6rem;
          }
        }

        .precio {
          font-family: v.$fuente_secundaria;
          font-size: 1.2rem;
          color: v.$dorado;
          font-weight: v.$bold;
          margin: 0;
          text-align: center;
          
          @include m.tablet {
            font-size: 1.3rem;
          }
          
          @include m.desktop {
            font-size: 1.2rem;
          }
        }
      }
    }

    .botones-producto {
      margin-top: 1.5rem;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      
      @include m.tablet {
        flex-direction: row;
        gap: 1.5rem;
      }

      a {
        flex: 1;
        @include m.boton(v.$dorado, block);
        margin: 0;
        
        @include m.tablet {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  // Dark mode
  .dark-mode & {
    background-color: v.$gris-oscuro;

    .resumen-propiedad {
      background-color: v.$gris-oscuro;
      color: v.$blanco;

      h2 {
        color: v.$dorado;
      }

      .detalles-producto p {
        color: v.$gris-claro;

        strong {
          color: v.$dorado;
        }

        .talla {
          background-color: v.$dorado;
          color: v.$negro;
        }
      }

      .iconos-caracteristicas li {
        color: v.$dorado;

        .icono {
          filter: invert(100%);
        }
      }

      .botones-producto a {
        background-color: v.$dorado;
        color: v.$blanco;

        &:hover {
          background-color: color.adjust(v.$dorado, $lightness: -10%);
        }
      }
    }
  }
}