@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    background-color: rgba(v.$dorado, 0.05);
    padding: 3rem;
    border-radius: 1.5rem;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    
    p {
        font-family: v.$fuente_secundaria;
        font-size: 1.6rem;
        color: v.$grisOscuro;
        margin: 2rem 0 0 0;
        line-height: 1.6;
    }

    legend {
        font-family: v.$fuente_logo;
        font-size: 2.4rem;
        color: v.$dorado;
        text-transform: uppercase;
        padding: 0 1rem;
        margin-bottom: 2rem;
        position: relative;
        
        &::after {
            content: '';
            position: absolute;
            bottom: -0.5rem;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: v.$dorado;
            opacity: 0.3;
        }
    }

    label {
        font-family: v.$fuente_secundaria;
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
        margin-bottom: 0.8rem;
        color: v.$grisOscuro;
        font-size: 1.4rem;
        letter-spacing: 0.5px;
    }

    input:not([type="submit"]), 
    textarea,
    select {
        font-family: v.$fuente_secundaria;
        padding: 1.2rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 2px solid rgba(v.$dorado, 0.2);
        border-radius: 0.8rem;
        margin-bottom: 2rem;
        font-size: 1.6rem;
        transition: all 0.3s ease;
        
        &:focus {
            outline: none;
            border-color: v.$dorado;
            box-shadow: 0 0 0 3px rgba(v.$dorado, 0.1);
        }
        
        &::placeholder {
            color: v.$gris;
            font-size: 1.4rem;
        }
    }

    textarea {
        height: 20rem;
        resize: vertical;
        min-height: 15rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 2rem 0;
        padding: 1rem;
        background-color: rgba(v.$dorado, 0.05);
        border-radius: 0.8rem;
    }

    input[type="radio"] {
        margin: 0;
        width: auto;
        accent-color: v.$dorado;
    }

    .imagen-small {
        border-radius: 0.8rem;
        border: 2px solid rgba(v.$dorado, 0.2);
        margin: 1rem 0;
    }

    @include m.tablet {
        padding: 4rem;
        
        legend {
            font-size: 2.8rem;
        }
        
        label {
            font-size: 1.5rem;
        }
        
        input:not([type="submit"]), 
        textarea,
        select {
            font-size: 1.7rem;
            padding: 1.4rem;
        }
    }

    // Estilos para la sección de tallas
    .tallas-container {
        background-color: transparent;
        padding: 1.5rem;
        border-radius: 0.8rem;
        margin: 1.5rem 0;
        border: none;
        
        legend {
            font-size: 1.8rem;
            margin-bottom: 1rem;
            color: v.$grisOscuro;
            font-family: v.$fuente_secundaria;
            font-weight: v.$bold;
            text-transform: uppercase;
            letter-spacing: 0.5px;
        }
        
        .tallas-opciones {
            display: flex;
            flex-wrap: wrap;
            gap: 1rem;
            
            label {
                flex: 1;
                min-width: 8rem;
                max-width: 12rem;
                display: flex;
                align-items: center;
                gap: 0.8rem;
                padding: 0.8rem 1.2rem;
                background-color: v.$blanco;
                border: 2px solid rgba(v.$dorado, 0.2);
                border-radius: 0.5rem;
                cursor: pointer;
                transition: all 0.3s ease;
                margin: 0;
                
                &:hover {
                    border-color: v.$dorado;
                    background-color: rgba(v.$dorado, 0.05);
                }
                
                input[type="checkbox"] {
                    width: 1.6rem;
                    height: 1.6rem;
                    margin: 0;
                    accent-color: v.$dorado;
                    cursor: pointer;
                }
                
                span {
                    font-size: 1.4rem;
                    font-weight: v.$bold;
                    color: v.$grisOscuro;
                    text-transform: capitalize;
                }
            }
        }
    }

    // Dark mode para tallas
    .dark-mode & {
        .tallas-container {
            background-color: transparent;
            
            legend {
                color: v.$blanco;
            }
            
            label {
                background-color: v.$gris-oscuro;
                border-color: rgba(v.$dorado, 0.3);
                
                &:hover {
                    border-color: v.$dorado;
                    background-color: rgba(v.$dorado, 0.1);
                }
                
                span {
                    color: v.$blanco;
                }
            }
        }
    }
}