@use 'base/variables' as v;
@use 'base/mixins' as m;



.entrada-blog {
    margin-bottom: 2rem;

    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.informacion-meta {
    span {
        color: v.$amarillo;
    }
}

.texto-entrada {
    a {
        color: v.$negro;
        margin-top: 2rem;
        display: block;  //Se pone block para la separacion ya que por ser enlace es inline 
        @include m.tablet {
            margin-top: 0;
        }
    }
    h4 {  //Se genera pseudo elemento de linea verde para tener la barra entre los blogs 
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: v.$verde;
            margin-top: 1rem;
        }
    }

}

.redes-sociales {
    margin-top: 0;
    margin-bottom: 0;
    text-align: center;
    background-color: v.$blanco;
    color: v.$dorado;
    padding: 40px;
    border-radius: 10px;
    transition: all 0.3s ease;
    border: 1px solid rgba(v.$dorado, 0.2);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);

    h2 {
        font-size: 2rem;
        margin-bottom: 15px;
        color: v.$dorado;
    }

    p {
        font-size: 1.2rem;
        margin-bottom: 20px;
        color: v.$negro;
    }

    .iconos-redes {
        display: flex;
        justify-content: center;
        gap: 15px;
    }

    .icono {
        padding: 12px 20px;
        border-radius: 5px;
        text-decoration: none;
        font-weight: bold;
        color: white;
        transition: 0.3s;
    }

    .instagram { background: #C13584; }
    .facebook { background: #1877F2; }
    .tiktok { 
        background: #000; 
        color: #fff; 
        border: 2px solid v.$dorado;
        
        &:hover {
            background: v.$dorado;
            color: v.$negro;
        }
    }

    .icono:hover {
        transform: scale(1.1);
        opacity: 0.8;
    }

    // Dark mode
    .dark-mode & {
        background-color: v.$grisOscuro;
        border: 1px solid rgba(v.$dorado, 0.3);
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);

        h2 {
            color: v.$dorado;
        }

        p {
            color: v.$blanco;
        }

        .icono {
            &.tiktok {
                background: v.$negro;
                border-color: v.$dorado;
                
                &:hover {
                    background: v.$dorado;
                    color: v.$negro;
                }
            }
        }
    }
}