/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-decoration: none;
  padding: 0.4rem 1rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.boton-amarillo:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
  cursor: pointer;
  transform: translateY(-2px);
}
@media (min-width: 768px) {
  .boton-amarillo {
    padding: 0.5rem 1.5rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 1024px) {
  .boton-amarillo {
    padding: 0.6rem 2rem;
    font-size: 1.8rem;
  }
}

