.dark-mode {
  background-color: rgb(76.5, 76.5, 76.5);
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/* UI */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  position: relative;
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
.header.inicio::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  font-family: "Bebas Neue", sans-serif;
  letter-spacing: 5px;
  text-align: left;
  color: #FFFFFF;
  font-size: 600;
  font-weight: 700;
  line-height: 2;
  max-width: 70rem;
}

.footer {
  margin-top: 0;
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.logo {
  max-width: 700px;
  height: auto;
  display: block;
}
.logo svg {
  width: 100%;
  height: auto;
  display: block;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  font-family: "Bebas Neue", sans-serif;
  letter-spacing: 2.5px;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/*Menu de hamburguesa*/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.no-webp .imagen-contacto {
  background-image: url("../img/contacto.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/contacto.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.redes-sociales {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  background-color: #FFFFFF;
  color: #D4AF37;
  padding: 40px;
  border-radius: 10px;
  transition: all 0.3s ease;
  border: 1px solid rgba(212, 175, 55, 0.2);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
.redes-sociales h2 {
  font-size: 2rem;
  margin-bottom: 15px;
  color: #D4AF37;
}
.redes-sociales p {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #000000;
}
.redes-sociales .iconos-redes {
  display: flex;
  justify-content: center;
  gap: 15px;
}
.redes-sociales .icono {
  padding: 12px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  color: white;
  transition: 0.3s;
}
.redes-sociales .instagram {
  background: #C13584;
}
.redes-sociales .facebook {
  background: #1877F2;
}
.redes-sociales .tiktok {
  background: #000;
  color: #fff;
  border: 2px solid #D4AF37;
}
.redes-sociales .tiktok:hover {
  background: #D4AF37;
  color: #000000;
}
.redes-sociales .icono:hover {
  transform: scale(1.1);
  opacity: 0.8;
}
.dark-mode .redes-sociales {
  background-color: #333333;
  border: 1px solid rgba(212, 175, 55, 0.3);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}
.dark-mode .redes-sociales h2 {
  color: #D4AF37;
}
.dark-mode .redes-sociales p {
  color: #FFFFFF;
}
.dark-mode .redes-sociales .icono.tiktok {
  background: #000000;
  border-color: #D4AF37;
}
.dark-mode .redes-sociales .icono.tiktok:hover {
  background: #D4AF37;
  color: #000000;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario {
  background-color: rgba(212, 175, 55, 0.05);
  padding: 3rem;
  border-radius: 1.5rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}
.formulario p {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  color: #333333;
  margin: 2rem 0 0 0;
  line-height: 1.6;
}
.formulario legend {
  font-family: "Bebas Neue", sans-serif;
  font-size: 2.4rem;
  color: #D4AF37;
  text-transform: uppercase;
  padding: 0 1rem;
  margin-bottom: 2rem;
  position: relative;
}
.formulario legend::after {
  content: "";
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #D4AF37;
  opacity: 0.3;
}
.formulario label {
  font-family: "Oswald", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  margin-bottom: 0.8rem;
  color: #333333;
  font-size: 1.4rem;
  letter-spacing: 0.5px;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  font-family: "Oswald", sans-serif;
  padding: 1.2rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 2px solid rgba(212, 175, 55, 0.2);
  border-radius: 0.8rem;
  margin-bottom: 2rem;
  font-size: 1.6rem;
  transition: all 0.3s ease;
}
.formulario input:not([type=submit]):focus,
.formulario textarea:focus,
.formulario select:focus {
  outline: none;
  border-color: #D4AF37;
