@use '../base/variables' as v;
@use '../base/mixins' as m;

.titulo-seccion {

    font-family: v.$fuente_logo;
    font-size: 2.8rem;
    text-align: center;
    margin: 2rem 0;
    font-weight: v.$bold;
    letter-spacing: 2px;
    color: v.$dorado;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 1rem;
    
    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 80px;
        height: 3px;
        background-color: v.$dorado;
        border-radius: 2px;
    }
    
    @include m.tablet {
        font-size: 3.2rem;
        margin: 3rem 0;
        letter-spacing: 3px;
        
        &::after {
            width: 120px;
        }
    }
    
    @include m.desktop {
        font-size: 3.8rem;
        margin: 4rem 0;
        letter-spacing: 4px;
        
        &::after {
            width: 150px;
            height: 4px;
        }
    }

}



.parrafo-seccion {
    font-family: v.$fuente_secundaria;
    font-size: 1.6rem;
    color: v.$negro;
    text-align: center;
    max-width: 800px;
    margin: 0 auto 3rem;
    line-height: 1.6;
    padding: 0 2rem;
    
    @include m.tablet {
        font-size: 1.8rem;
        margin-bottom: 4rem;
    }
    
    @include m.desktop {
        font-size: 2rem;
        margin-bottom: 5rem;
    }
} 