@use 'base/variables' as v;
@use 'base/mixins' as m;


.valores {
  text-align: center;
  margin: 5rem 0;

  h2 {
    color: red;
    font-size: 3rem;
    margin-bottom: 4rem;
  }

  .valores-grid {
    display: grid;
    gap: 3rem;
    justify-content: center;
    max-width: 1200px;
    margin: 0 auto; // Centra el grid horizontalmente


    @include m.tablet {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  .valor {
    padding: 2rem;
    background-color: v.$gris-oscuro;
    border-radius: 1rem;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    color: v.$blanco;

    h3 {
      font-size: 2.4rem;
      margin: 2rem 0 1rem;
    }

    p {
      color: v.$gris-claro;
    }

    .imagen-valor {
      height: 40rem;
      background-size: cover;
      background-position: center;
      border-radius: 1rem;
    }

    .estilo {
      background-image: url('/build/img/estilo.jpg'); // 🔁 aquí subes tu imagen
    }

    .calidad {
      background-image: url('/build/img/calidad.jpg'); // 🔁 aquí subes tu imagen
    }

    .garantia {
      background-image: url('/build/img/garantia.jpg'); // 🔁 aquí subes tu imagen
    }
  }
}
