@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); // 2 columnas en móvil
    
    @include m.tablet {
        grid-template-columns: repeat(3, 1fr); // 3 columnas en tablet
    }
    
    @include m.desktop {
        grid-template-columns: repeat(4, 1fr); // 4 columnas en desktop
    }

    .tarjeta-producto {
        margin: 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        border-radius: 1rem;
        overflow: hidden; // Asegura que la imagen no se desborde
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
        
        .img-anuncios {
            width: 100%;
            height: 200px;
            object-fit: cover; // Mantiene la proporción y cubre el espacio
            object-position: center; // Centra la imagen
            display: block; // Elimina espacios extra
            
            @include m.tablet {
                height: 250px;
            }
            
            @include m.desktop {
                height: 300px;
            }
        }
        
        .resumen-propiedad {
            flex: 1;
            display: flex;
            flex-direction: column;
            padding: 1.5rem;
            
            h3 {
                font-family: v.$fuente_logo;
                font-size: 2.2rem;
                margin: 0 0 1rem 0;
                line-height: 1.2;
                color: v.$dorado;
                text-transform: uppercase;
                letter-spacing: 1px;
                text-align: center;
                
                @include m.tablet {
                    font-size: 2.4rem;
                }
                
                @include m.desktop {
                    font-size: 2.6rem;
                }
            }
            
            .detalles-producto {
                flex: 1;
                margin-bottom: 1.5rem;
            }

            .iconos-caracteristicas {
                margin: 1rem 0;
                display: flex;
                justify-content: center;
                padding: 0;
                list-style: none;
                width: 100%;
                
                @include m.tablet {
                    justify-content: flex-start;
                }

                li {
                    display: flex;
                    align-items: center;
                    gap: 0.8rem;
                    font-size: 1.4rem;
                    color: v.$dorado;
                    font-weight: v.$bold;
                    background-color: rgba(v.$dorado, 0.1);
                    padding: 0.4rem 1rem;
                    border-radius: 0.5rem;
                    width: auto;
                    min-width: 0;
                    max-width: 100%;
                    justify-content: center;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    
                    @include m.tablet {
                        font-size: 1.3rem;
                        padding: 0.5rem 1.2rem;
                    }
                    
                    @include m.desktop {
                        font-size: 1.4rem;
                        padding: 0.4rem 1rem;
                    }
                    
                    .icono {
                        width: 1.6rem;
                        height: 1.6rem;
                        flex-shrink: 0;
                        filter: brightness(0) saturate(100%) invert(47%) sepia(45%) saturate(1012%) hue-rotate(358deg) brightness(89%) contrast(87%);
                        
                        @include m.tablet {
                            width: 1.8rem;
                            height: 1.8rem;
                        }
                        
                        @include m.desktop {
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                    
                    .precio {
                        font-family: v.$fuente_secundaria;
                        font-size: 1.2rem;
                        color: v.$dorado;
                        font-weight: v.$bold;
                        margin: 0;
                        text-align: center;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        
                        @include m.tablet {
                            font-size: 1.3rem;
                        }
                        
                        @include m.desktop {
                            font-size: 1.2rem;
                        }
                    }
                }
            }
            
            .botones-producto {
                margin-top: auto;
                display: flex;
                flex-direction: column;
                gap: 0.8rem;
                justify-content: center;
                align-items: center;
                
                @include m.tablet {
                    flex-direction: row;
                    gap: 1rem;
                    justify-content: center;
                }
                
                @include m.desktop {
                    gap: 0.8rem;
                }
                
                a {
                    text-align: center;
                    padding: 0.8rem;
                    border-radius: 0.5rem;
                    font-weight: v.$bold;
                    text-transform: uppercase;
                    font-size: 1.2rem;
                    font-family: v.$fuente_secundaria;
                    min-width: 10rem;
                    width: 100%;
                    max-width: 10rem;
                    
                    @include m.tablet {
                        width: 10rem;
                        font-size: 1.3rem;
                    }
                    
                    @include m.desktop {
                        font-size: 1.2rem;
                        padding: 0.7rem;
                    }
                    
                    &:first-child {
                        background-color: v.$dorado;
                        color: v.$blanco;
                        
                        &:hover {
                            background-color: darken(v.$dorado, 10%);
                        }
                    }
                }
            }
        }
    }
} 