@use 'base/variables' as v;
@use 'base/mixins' as m;

.panel-admin {
    &__contenedor {
        padding: 2rem 1rem;
        
        @include m.tablet {
            padding: 4rem 2rem;
        }
        
        @include m.desktop {
            padding: 6rem 4rem;
        }
    }

    &__botones {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-bottom: 3rem;
        padding: 1.5rem;
        background-color: rgba(v.$dorado, 0.05);
        border-radius: 1rem;
        box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
        
        @include m.tablet {
            flex-direction: row;
            gap: 2rem;
            justify-content: center;
            padding: 2rem;
            margin-bottom: 4rem;
        }
        
        .boton {
            width: 100%;
            text-align: center;
            padding: 1.2rem;
            font-size: 1.4rem;
            transition: all 0.3s ease;
            min-width: 20rem;
            
            &:hover {
                transform: translateY(-2px);
                box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
            }
            
            @include m.tablet {
                width: 20rem;
                font-size: 1.6rem;
            }
        }
    }

    &__tabla {
        &-contenedor {
            overflow-x: auto;
            margin-bottom: 4rem;
            background-color: v.$blanco;
            border-radius: 1rem;
            box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
            padding: 1rem;
            
            @include m.tablet {
                padding: 2rem;
                margin-bottom: 6rem;
            }
        }

        width: 100%;
        min-width: 600px;
        border-collapse: separate;
        border-spacing: 0;
        
        th {
            background-color: v.$dorado;
            color: v.$blanco;
            padding: 1.2rem;
            text-align: left;
            font-family: v.$fuente_secundaria;
            font-size: 1.3rem;
            text-transform: uppercase;
            letter-spacing: 1px;
            white-space: nowrap;
            
            &:first-child {
                border-top-left-radius: 0.8rem;
            }
            
            &:last-child {
                border-top-right-radius: 0.8rem;
            }
            
            @include m.tablet {
                font-size: 1.5rem;
                padding: 1.5rem;
            }
        }
        
        td {
            padding: 1.2rem;
            border-bottom: 1px solid rgba(v.$dorado, 0.1);
            font-family: v.$fuente_secundaria;
            font-size: 1.3rem;
            vertical-align: middle;
            
            @include m.tablet {
                font-size: 1.5rem;
                padding: 1.5rem;
            }
        }

        &-id {
            width: 5%;
            font-weight: v.$bold;
            color: v.$dorado;
            text-align: center;
        }

        &-titulo {
            width: 25%;
            font-weight: v.$bold;
            color: v.$grisOscuro;
            
            @media (max-width: 768px) {
                max-width: 150px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &-imagen {
            width: 20%;
            
            img {
                width: 8rem;
                height: 8rem;
                object-fit: cover;
                border-radius: 0.8rem;
                border: 2px solid rgba(v.$dorado, 0.2);
                transition: all 0.3s ease;
                
                &:hover {
                    transform: scale(1.05);
                    border-color: v.$dorado;
                }
                
                @include m.tablet {
                    width: 12rem;
                    height: 12rem;
                }
                
                @include m.desktop {
                    width: 15rem;
                    height: 15rem;
                }
            }
        }

        &-precio {
            width: 15%;
            font-weight: v.$bold;
            color: v.$dorado;
            font-size: 1.4rem;
            
            @include m.tablet {
                font-size: 1.6rem;
            }
        }

        &-acciones {
            width: 20%;
            
            .botones-accion {
                display: flex;
                flex-direction: column;
                gap: 0.8rem;
                
                @include m.tablet {
                    flex-direction: row;
                    gap: 1rem;
                }
                
                form {
                    flex: 1;
                }
                
                a, input[type="submit"] {
                    width: 100%;
                    text-align: center;
                    padding: 1rem;
                    font-size: 1.3rem;
                    transition: all 0.3s ease;
                    min-width: 12rem;
                    
                    &:hover {
                        transform: translateY(-2px);
                    }
                    
                    @include m.tablet {
                        width: 12rem;
                    }
                }
            }
        }
    }

    &__alerta {
        margin-bottom: 2rem;
        padding: 1.2rem;
        border-radius: 0.8rem;
        font-size: 1.4rem;
        font-weight: v.$bold;
        text-align: center;
        
        @include m.tablet {
            margin-bottom: 3rem;
            padding: 1.5rem;
            font-size: 1.6rem;
        }
        
        &.exito {
            background-color: rgba(40, 167, 69, 0.1);
            color: #28a745;
            border: 2px solid #28a745;
        }
    }
}

// Dark mode
.dark-mode {
    .panel-admin {
        &__botones {
            background-color: rgba(v.$dorado, 0.1);
        }
        
        &__tabla {
            &-contenedor {
                background-color: v.$gris-oscuro;
            }
            
            th {
                background-color: v.$dorado;
                color: v.$negro;
            }
            
            td {
                color: v.$blanco;
                border-bottom-color: rgba(v.$dorado, 0.2);
                
                &:hover {
                    background-color: rgba(v.$dorado, 0.15);
                }
            }
            
            &-titulo {
                color: v.$blanco;
            }
            
            &-imagen img {
                border-color: rgba(v.$dorado, 0.3);
                
                &:hover {
                    border-color: v.$dorado;
                }
            }
        }
        
        &__alerta.exito {
            background-color: rgba(40, 167, 69, 0.2);
            border-color: #28a745;
        }
    }
} 